import React from "react";
import { SelectProps } from "./types";
import styles from "./select.module.scss";

const Select = ({
  children = "",
  className = {},
  item = [],
  label,
  placeholder,
  ...rest
}: SelectProps) => {
  const stylesApplied = [styles.select, className];

  return (
    <div className={styles.container}>
      <label className={styles.label} htmlFor={label}>{label}</label>
        <select id={label} className={stylesApplied.join(" ")} {...rest}>
          {placeholder ? <option value={''} selected>{placeholder}</option> : null}
          {item.map((a: any) => (
            <option value={a} key={a}>
              {a}
            </option>
          ))}
        </select>
    </div>
  );
};

export default Select;
