import { useCallback, useEffect, useState } from "react";
import CheckBox from "../../components/checkbox/checkbox";
import ContactUs from "../../components/contact-us";
import Footer from "../../components/footer";
import Select from "../../components/select";
import { SubViewPlanCard, ViewPlanCard } from "./view-plan-card/view-pan-card";
import style from "./view-plans.module.scss";

const plans = [
  {
    title: "Baby Bear",
    practice: [
      "Topic wise questions",
      "All sections covered",
      "1000+ Questions ",
      "Basic reporting",
    ],
    tests: ["10 Mock tests", "All sections covered", "Basic reporting"],
    pracAmt: 1000,
    testAmt: 500,
  },
  {
    title: "Papa Bear",
    practice: [
      "Topic wise questions",
      "All sections covered",
      "3000+ Questions ",
      "Advanced reporting",
    ],
    tests: [
      "20 Mock tests",
      "All sections covered",
      "Personalized reporting",
      "Highlighting important areas",
    ],
    pracAmt: 2000,
    testAmt: 1500,
  },
  {
    title: "Mama Bear",
    practice: [
      "Topic wise questions",
      "All sections covered",
      "5000+ Questions ",
      "New questions added every month",
      "Advanced reporting",
    ],
    tests: [
      "30 Mock tests",
      "Mini mock tests",
      "All sections covered",
      "Personalized reporting",
      "Highlighting important areas",
    ],
    pracAmt: 3000,
    testAmt: 2500,
  },
];

const ViewPlans = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [showDetail, setShowDetails] = useState(false);
  const [data, setData] = useState({} as any);
  const [total, setTotal] = useState(0);

  const [selected, setSelected] = useState({
    Practice: true,
    Tests: true,
  } as any);

  const onViewMore = useCallback((item: any) => {
    setShowDetails(true);
    setData(item);
  }, []);

  useEffect(() => {
    let sum = 0;
    if (selected.Practice) sum += data?.pracAmt;
    if (selected.Tests) sum += data?.testAmt;
    setTotal(sum);
  }, [selected, data?.pracAmt, data?.testAmt]);

  const onChangeLocal = useCallback((value: string, checked: boolean) => {
    setSelected((prev: any) => ({ ...prev, [value]: checked }));
  }, []);

  const subCard = (title: any, a: any) => (
    <div className={style.subCard}>
      <div className={style.title}>
        <CheckBox
          label={title}
          isSelected={selected[title]}
          value={title}
          onChange={onChangeLocal}
        />
      </div>
      <hr className={style.hrLine} />
      <div className="d-center d-col">
        {a.map((a: any, b: any) => (
          <div className={style.subText} key={b}>
            {a}
          </div>
        ))}
      </div>
    </div>
  );

  return (
    <>
      {showDetail ? (
        <div className={style.mcontainer}>
          <div className={style.backText} onClick={() => setShowDetails(false)}>View all plans</div>
          <div>{data?.title} - Plan details</div>
          <div className={style.amount}>{total}$</div>
          {subCard("Practice", data?.practice)}
          {subCard("Tests", data?.tests)}
        </div>
      ) : (
        <div className={style.container}>
          <h2>Choose your annual plan</h2>
          <div className={style.subText}>that’s right for you</div>
          <div className={`d-flex j-space-between d-row ${style.select}`}>
            <Select
              item={["NSW", "NSWA"]}
              placeholder="Select region"
              className={style.mr5}
            />
            <Select item={["NSW", "NSWB"]} placeholder="Select exam type" />
          </div>
          <div className={style.planCards}>
            {plans.map((item, i) => (
              <ViewPlanCard
                key={i}
                data={item}
                isActive={activeIndex === i}
                onClick={() => setActiveIndex(i)}
                onViewMore={onViewMore}
              />
            ))}
          </div>
        </div>
      )}
      <ContactUs />
    </>
  );
};

export default ViewPlans;
