import { useCallback, useEffect, useMemo, useState } from "react";
import { backBear, frontBear } from "../../../assets/images";
import { Button } from "../../../components";
import CheckBox from "../../../components/checkbox/checkbox";
import style from "./view-card.module.scss";

export const SubViewPlanCard = (props: any) => {

  const { title, item, onChange } = props;

  const [selected, setSelected] = useState({
    Practice: true,
    Tests: true,
  } as any);

  const onChangeLocal = useCallback((value: string, checked: boolean) => {
    setSelected((prev: any) => ({ ...prev, [value]: checked }));
  }, []);

  useEffect(() => {
    onChange(selected);
  }, [onChange, selected]);


  return (
    <div className={style.subCard}>
      <div className={style.title}>
        <CheckBox
          label={title}
          isSelected={selected[title]}
          value={title}
          onChange={onChangeLocal}
        />
      </div>
      <hr className={style.hrLine} />
      <div className="d-center d-col">
        {item.map((a: any, b: any) => (
          <div className={style.subText} key={b}>{a}</div>
        ))}
      </div>
    </div>
  );
}

export const ViewPlanCard = (props: any) => {
  const { isActive, data, value, onClick, onViewMore } = props;
  const [total, setTotal] = useState(data?.pracAmt + data?.testAmt);

  const onChange = useCallback((changed: any) => {
    let sum = 0;
    if (changed.Practice) sum += data?.pracAmt;
    if (changed.Tests) sum += data?.testAmt; 
    setTotal(sum);
  }, [data?.pracAmt, data?.testAmt]);


  return (
    <div
      className={`${style.container} ${isActive ? style.active : ""}`}
      onClick={onClick}
    >
      {isActive ? <span className={style.checkbox}><CheckBox isRadio={true} isSelected={isActive} value={value} /></span>  : null }
      <img src={isActive ? frontBear : backBear} alt="bear" />
      <div className={style.head}>{data?.title}</div>
      <div className={`${style.amount} ${style.xShow}`}><>{total}$</></div>
      <SubViewPlanCard title='Practice' item={data?.practice} onChange={onChange} />
      <SubViewPlanCard title='Tests' item={data?.tests} onChange={onChange}/>
      <div className={`d-center j-space-between w-100 mt-10 ${style.viewCont}`}>
        <div className={style.more}><span className={style.xShow} onClick={() => onViewMore(data)}>View more info</span></div>
        <div className={`${style.amount} ${style.LShow}`}><>{total}$</></div>
      </div>
      {isActive ? (
        <Button className={`mt-10 ${style.regBtn}`}>Register now</Button>
      ) : null}
    </div>
  );
};
