import React, { useState } from "react";
import { CheckBoxProps } from "./types";
import styles from "./checkbox.module.scss";

const CheckBox = ({
  className = {},
  label,
  isRadio,
  value,
  onChange = () => {},
  isSelected = false
}: CheckBoxProps) => {
  const stylesApplied = [styles.container, isRadio ? styles.radio : ""];

  return (
    <label className={stylesApplied.join(" ")}>
      {label}
      <input
        type="checkbox"
        checked={isSelected}
        onChange={() => onChange(value, !isSelected)}
      />
      <span className={styles.checkmark}></span>
    </label>
  );
};

export default CheckBox;
